













































































































import ActionDialog from '@/components/generic/ActionDialog.vue';
import Users from '@/components/global/groups/Users.vue';
import CopyButton from '@/components/generic/CopyButton.vue';
import {SnackbarModule} from '@/store/snackbar';
import {IApiKey, IGroup} from '@heimdall/interfaces';
import axios from 'axios';
import Vue from 'vue';
import Component from 'vue-class-component';
import {Prop} from 'vue-property-decorator';

@Component({
  components: {
    ActionDialog,
    CopyButton,
    Users
  }
})
export default class GroupAPIKeysModal extends Vue {
  @Prop({
    type: Object,
    required: true
  })
  readonly group!: IGroup;

  dialog = false;
  loading = false;
  apiKeys: IApiKey[] = [];
  search = '';
  password = '';
  loadingText = 'Loading...';
  noDataText = 'No API Keys';
  noResultsText = 'No API Keys found';
  apiKeyHeaders = [
    {
      text: 'Name',
      value: 'name',
      filterable: true,
      width: '20%',
      align: 'start'
    },
    {
      text: 'Value',
      value: 'apiKey',
      sortable: false
    },
    {
      text: 'Action',
      value: 'action',
      sortable: false
    }
  ];

  mounted() {
    this.updateAPIKeys();
  }

  updateAPIKeys() {
    this.loading = true;
    axios
      .get<IApiKey[]>(`/apikeys`, {
        params: {
          groupId: this.group.id
        }
      })
      .then(({data}) => {
        this.apiKeys = data;
        this.loading = false;
      })
      .catch((error) => {
        this.loading = false;
        // Default error handling works fine
        throw error;
      });
  }

  refreshAPIKey(item: IApiKey) {
    this.loading = true;
    axios
      .delete<IApiKey>(`/apikeys/${item.id}`, {
        data: {
          currentPassword: this.password
        }
      })
      .then(() => {
        this.apiKeys = this.apiKeys.filter((key) => key.id !== item.id);

        // Re-create the key
        axios
          .post('/apikeys', {
            groupId: this.group.id,
            name: item.name,
            currentPassword: this.password
          })
          .then(({data}) => {
            this.apiKeys.push(data);
            SnackbarModule.notify('API Key recreated successfully');
            this.loading = false;
          })
          .catch((error) => {
            this.loading = false;
            // Default error handling works fine
            throw error;
          });
      })
      .catch((error) => {
        this.loading = false;
        // Default error handling works fine
        throw error;
      });
  }

  addAPIKey() {
    this.loading = true;
    axios
      .post<IApiKey>('/apikeys', {
        groupId: this.group.id,
        currentPassword: this.password
      })
      .then(({data}) => {
        this.apiKeys.push(data);
        SnackbarModule.notify('API Key added successfully');
        this.loading = false;
      })
      .catch((error) => {
        this.loading = false;
        // Default error handling works fine
        throw error;
      });
  }

  setKeyName(item: IApiKey) {
    this.loading = true;
    axios
      .put(`/apikeys/${item.id}`, {
        name: item.name,
        currentPassword: this.password
      })
      .then(() => {
        SnackbarModule.notify('API Key name updated successfully');
        this.loading = false;
      })
      .catch((error) => {
        this.loading = false;
        // Default error handling works fine
        throw error;
      });
  }

  deleteAPIKey(item: IApiKey) {
    this.loading = true;
    axios
      .delete<IApiKey>(`/apikeys/${item.id}`, {
        data: {
          currentPassword: this.password
        }
      })
      .then(({data}) => {
        this.apiKeys = this.apiKeys.filter((key) => key.id !== data.id);
        SnackbarModule.notify('API Key deleted successfully');
        this.loading = false;
      })
      .catch((error) => {
        this.loading = false;
        // Default error handling works fine
        throw error;
      });
  }
}
