





























import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
  components: {}
})
export default class PrintButton extends Vue {
  showingModal = false;
  /**
   * Invoked when file(s) are loaded.
   */
  closeModal() {
    this.showingModal = false;
  }

  showModal() {
    this.showingModal = true;
  }
}
